import React, { Component } from 'react';
import styled from 'styled-components';

import { Switch } from './reactComponentLib';

const StyledDiv = styled.div`
  padding: 25px;

  ${Switch.Components.Button} {
    margin-bottom: 15px;
  }
`;

class App extends Component {
  render() {
    return (
      <div>
        <StyledDiv>
          <h1>Switch</h1>
          <Switch initialValue={true} />
          <Switch on="On" off="Off" />
          <Switch on="On" off="Off" activeColor="#ea5455" />
          <Switch onChange={v => alert(v)} />
        </StyledDiv>
      </div>
    );
  }
}

export default App;
